'use client';

interface DetailRowProps {
  label: string;
  value: string | number | null | undefined;
}

function DetailRow({ label, value }: DetailRowProps) {
  return (
    <div className="flex py-2 border-b last:border-b-0">
      <span className="w-1/3 text-sm text-gray-600">{label}</span>
      <span className="w-2/3 text-sm">{value || '-'}</span>
    </div>
  );
}

interface DetailViewProps {
  data: Record<string, any>;
  excludeKeys?: string[];
}

export default function DetailView({ data, excludeKeys = [] }: DetailViewProps) {
  return (
    <div className="bg-white rounded-lg p-4 space-y-2">
      {Object.entries(data)
        .filter(([key]) => !excludeKeys.includes(key) && key !== 'id')
        .map(([key, value]) => (
          <DetailRow
            key={key}
            label={key.split('-').map(word => 
              word.charAt(0).toUpperCase() + word.slice(1)
            ).join(' ')}
            value={value}
          />
        ))}
    </div>
  );
} 